import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { InventoryComponent } from './inventory.component';
import { InventorySetupComponent } from './components/inventory-setup/inventory-setup.component';
import { InventoryCreateComponent } from './components/inventory-create/inventory-create.component';
import { InventoryUpdateComponent } from './components/inventory-update/inventory-update.component';
import { InventoryViewComponent } from './components/inventory-view/inventory-view.component';
import { InventoryReportsComponent } from './components/inventory-reports/inventory-reports.component';
import { InventoryCurrentDetailsComponent } from './components/inventory-current-details/inventory-current-details.component';
import { InventoryVendorManagementComponent } from './components/inventory-vendor-management/inventory-vendor-management.component';
import { InventoryInitialStockComponent } from './components/inventory-initial-stock/inventory-initial-stock.component';
import { InventoryInternalTransferComponent } from './components/inventory-internal-transfer/inventory-internal-transfer.component';
import { InventoryReceivingComponent } from './components/inventory-receiving/inventory-receiving.component';
import { InventoryCreateItemComponent } from './components/inventory-create-item/inventory-create-item.component';
import { InventoryStockAdjustmentsComponent } from './components/inventory-stock-adjustments/inventory-stock-adjustments.component';

const routes: Routes = [

	{ path: '', redirectTo: 'settings', pathMatch: 'full' },
	{ path: 'settings', component: InventorySetupComponent },
	{ path: 'create-inventory', component: InventoryCreateComponent },
	{ path: 'edit-inventory/:id', component: InventoryCreateComponent },
	{ path: 'current-inventory', component: InventoryCurrentDetailsComponent },
	{ path: 'vendor-management', component: InventoryVendorManagementComponent },
	{ path: 'initial-stock', component: InventoryInitialStockComponent },
	{ path: 'internal-transfer', component: InventoryInternalTransferComponent },
	{ path: 'receiving', component: InventoryReceivingComponent },
	{ path: 'create-item', component: InventoryCreateItemComponent },
	{ path: 'stock-adjustments', component: InventoryStockAdjustmentsComponent },
	{ path: 'view', component: InventoryViewComponent },
	{ path: 'view/:id', component: InventoryViewComponent},
	{ path: 'reports', component: InventoryReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InventoryRoutingModule { }
