    getCurrentInventory(){
      let params:any = {};
      params.apartmentId = parseInt(this.cookieService.get('apartmentId'));
       this.inventoryService.getAllInventoryByApartmentId(params).subscribe((res:any) => {
        if(res) {
           console.log(res);          
          }
       
        this.gridSourceData = {
          localdata:  res.length > 0 ? res:[],
          datatype: "array"
        }  
        this.inventoryHistoryData = new jqx.dataAdapter(this.gridSourceData);
       });
    
    }
